.soporte-login {
    display: none;
    left: 50%;
    height: 50%;
    padding: 2rem 3rem;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    width: 60%;
    z-index: 10;

    @include desktop {
        width: 45%;
        ;
    }

    // glassmorphism
    background: rgba(255, 255, 255, 0.25);
    box-shadow: 0 8px 32px 0 #2f343e;
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);

    h1 {
        margin-bottom: 1rem;
    }

    form {
        display: flex;
        flex-direction: column;

        input,
        select {
            border: none;
            border-radius: 5px;
            font-size: 2rem;
            margin: 1rem 0;
            padding: 1rem 1.4rem;

            @include mini-phone {
                padding: .5rem 1rem;
                font-size: 1.5rem;
            }

            @include phone {
                font-size: 1.6rem;
            }
        }

        .form-buttons {
            display: flex;
            justify-content: space-around;

            input {
                display: flex;
                justify-content: center;
                flex: 1;
                transition: all .3s ease;

                &:hover {
                    background-color: $azulOscuro;
                }
            }
        }

        .form-buttons input[type=button] {
            margin-left: 1rem;
        }

    }
}

.show {
    display: block;
}

.soporte-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    width: 100%;

    p {
        @include mini-phone {
            font-size: 1.6rem;
        }
    }

    #close-login {
        position: absolute;
        top: 0;
        left: 0;
        font-weight: 900;
        padding: 4px 10px;
        border: 2px solid;
        border-radius: 5px;

        &:hover {
            border: 2px solid $celeste;
            color: $celeste;
            cursor: pointer;
        }
    }
}

.soporte-logueado {
    p {
        margin-bottom: 3rem;
    }
}