.App {
    text-align: center;
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    height: 100vh;
    position: relative;
}

.container {
    height: 90%;
}

.blur {
    color: $azulOscuro;
    filter: blur(5px) brightness(0.7);
    pointer-events: none;
    user-select: none;
}

.btn {
    background-color: $azulClaro;
    border: none;
    border-radius: 5px;
    color: $grisClaro;
    padding: 1rem 2rem;

    &:hover {
        cursor: pointer;
    }
}