.footer {
    align-items: center;
    background-color: #282c34;
    column-gap: .5rem;
    display: flex;
    height: 10%;
    justify-content: center;
    padding: 1rem;
    position: relative;

    a {
        color: #0fe;
        transition: all .3s ease;
        text-shadow: 0 0 6px #00fff1;
        text-decoration: none;
    }

    .soporte {
        position: absolute;
        left: 3%;

        @include mini-phone {
            display: none;
        }

        @include phone {
            display: none;
        }

        #config {
            transition: all .3s ease;
        }

        &:hover {
            cursor: pointer;

            #config {
                fill: #0fe
            }
        }
    }
}