.header {
    // position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;

    .contador {
        margin-left: 5rem;
        font-size: 3.4rem;
        background-color: $azulClaro;
        padding: 1rem 2rem;

        @include phone {
            margin-left: 0;
            font-size: 3rem;
        }

        p {
            color: #0fe;
            margin: 0;
            font-weight: bold;
            font-size: 3rem;
        }

        @include phone {
            font-size: 2.6rem;

            p {
                font-size: 2.6rem;

            }
        }

        @include mini-phone {
            margin-left: 0;
            font-size: 1.9rem;
            padding: .5rem 1rem;

            p {
                font-size: 2.6rem;

            }
        }
    }

}