html {
    font-size: 62.5%;
}


body {
    font-size: 1.6rem;
    color: $grisClaro;
}

h1 {
    margin: 0;
    padding: .5rem;

    @include mini-phone {
        font-size: 1.7rem;
    }

    @include phone {
        font-size: 2.4rem;
    }

    @include tablet {
        font-size: 3.8rem;
    }

    @include desktop {
        font-size: 3.8rem;
    }
}

p {
    margin: 0;
    font-size: 2rem;

    @include tablet {
        font-size: 2.6rem;
    }

    @include desktop {
        font-size: 2.6rem;
    }
}